import React,{useState, useEffect} from "react";
import './App.css';
import {Card,Button,Space} from "antd";
import reportWebVitals from './reportWebVitals';
const clientId =`?client_id=${process.env.React_App_Key}`;
const mainUrl = `http://api.unsplash.com/photos/`;
const searchUrl = `http://api.unsplash.com/search/photos/`;


function App() {
  const [loading, setLoading] = useState(false);
  const [photos, setPhotos] = useState([]);
  const [page, setPage] = useState(0);
  const [query, setQuery] = useState("");

  useEffect(()=> {
    fetchImage();
  },[page]);

  const fetchImage = async () => {
    setLoading(true);
    let url;
    const urlPage = `&page=${page}`;
    const urlQuery = `&query=${query}`;
    if(query ){
      url = `${searchUrl}${clientId}${urlPage}${urlQuery}`
    } else{
      url = `${mainUrl}${clientId}${urlPage}`;
    }
    try {
      const response = await fetch(url);
      const data = await response.json();
      console.log("data", data);
      setPhotos((oldPhotos)=> {
        if(query && page === 1){
          return data.results;
        } else if(query) {
          return [...oldPhotos, ...data.results]
        } else{
          return [...oldPhotos, ...data]
        }
      });
      setLoading(false);
    } catch(error){
      console.log(error);
      setLoading(false);
    }
  };


  useEffect(()=> {
    const event = window.addEventListener("scroll",()=> {
      if((!loading && window.innerHeight + window.scrollY) >= document.body.scrollHeight - 2) {
        setPage ((oldPage) => {
          return oldPage + 1
        });
      }
    });
    return () => window.removeEventListener ("scroll", event);

  },[]);
  const handleSubmit= (e)=>{
    e.preventDefault();
    setPage(1);
  }

  return (
   <div className="container"> 
 <space style={{marginBottom: 15, marginTop: 10}}>
  <input
    type="text"
    placeholder="Search image"
    value={query}
    style= {{ width:300, marginBottom: 8 }}
    onChange={(e) => setQuery(e.target.value)}
    className="form-control"
  />
<button type="primary" onClick={handleSubmit}
style={{borderRadius:5,backgroundColor: "blue" , color: "white", marginBottom:5}}

>Search</button>
 </space>
 <div className="row">
  {photos.map((image, index) => (
    <div key={index} className="col-md-4">
      <Card
      cover={
        <img src={image.urls.regular}
        alt="image"
        style={{height: "170px", objectFit:"cover"}}
        />
      }
      
      />
    </div>
  ))}
 </div>
   </div>
  );
}

export default App;
